import Vue from 'vue'
import uniLoadMore from "@/components/uni-load-more.vue"
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			tabBars: '', //知识分类
			scrollLeft: 0,
			tabIndex: 0,
			authKey:'',
			symbolId:'',
			lists:'',
			loadingType: 0,
			next : -1,
			start: 0,
			size: 15,
			record: false,
			bottom: false,
			packets:false,
			user:false,
			give:'',
			token:'',
			userInfo:'',
			$static:'',
			pullDown: false,
			promotion: false,
			activity: true,
			keyword: ''
		};
	},
	onLoad: function(e) {
		this.token = e.token;
		this.$static = this.static
		if(this.token){
			this.packets = true;
		}else{
			this.packets = false;
		}
		this.authKey = uni.getStorageSync('authKey')
		if (this.authKey){
			this.checkSession()
		}
		this.requestLearn()
	},
	onShow() {
		this.userInfo = uni.getStorageSync('userInfo');
		if (this.authKey){
			this.coup()
		}
		this.keyword = uni.getStorageSync('keyword')
		if (this.keyword){
			uni.setStorageSync('keyword', '')
			console.log('----------------------------------------')
			console.log(this.keyword)
			this.start = 0
			this.requestLearnList()
		}
	},
	onPullDownRefresh() {
		this.start = 0
		this.keyword = uni.getStorageSync('keyword')
		this.requestLearnList()
		this.pullDown = true
	},
	onReachBottom(){
		console.log('reach bottom')
		if (this.loadingType == 2) {
			return
		}
		this.requestLearnList(false)
	},
	
	methods: {
		search(){
			uni.navigateTo({
				url: '/pages/knowledge/search'
			})
		},
		getElSize(id) { //得到元素的size
		console.log(id)
			return new Promise((res, rej) => {
				uni.createSelectorQuery().select("#" + id).fields({
					size: true,
					scrollOffset: true
				}, (data) => {
					res(data)
				}).exec()
			})
		},
		async tapTab(e) { //点击tab-bar
			this.symbolId = e.currentTarget.id
			console.log(this.symbolId)
			if (this.tabIndex === e.target.dataset.current) {
				return false;
			} else {
				let tabBar = await this.getElSize("tab-bar"),
					tabBarScrollLeft = tabBar.scrollLeft; //点击的时候记录并设置scrollLeft
				this.scrollLeft = tabBarScrollLeft;
				this.isClickChange = true;
				this.tabIndex = e.target.dataset.current;
			}
			this.start = 0
			this.requestLearnList()
		},
		// 获取的优惠券
		coup(){
			uni.request({
				url: this.http+'home/showCoupon',
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {
					token:this.authKey
				},
				success: res => {
					if (res.data.code == 200) {
						this.give = res.data.data
					}
				},
			});
		},
		// 领取优惠券
		btn(){
			uni.request({
				url: this.http+'home/receiveCoupon',
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {
					token:this.token
				},
				success: res => {
					if (res.data.code == 200) {
						uni.showToast({
							title: '领取优惠成功',
							icon:'none',
							mask: false,
							duration: 1500
						});
						this.packets = false;
					}
				},
			});
		},
		wxGetUserInfo:function(res){
			let that = this
			if (!res.detail.iv) {
				uni.showToast({ 
					title: "您取消了授权,登录失败", 
					icon: "none" ,
				}); 
				return false; 
			}
			let simpleUser = res.detail.userInfo
			let encryptedData = res.detail.encryptedData
			let iv = res.detail.iv
			uni.login({
				provider: 'weixin',
			    success(res){
				  console.log(res.code,'login')
				  var data = res.code
			    if (res.code) {
				  uni.request({
				  	url: that.http + 'user/token',
				  	method: 'GET',
				  	data: {
				  		login_code: res.code,
						encrypted_data:encryptedData,
						iv: iv
				  	},
				  	success: res => {
				  		let auth = res.data.data.authkey
						that.authKey = auth
						that.userInfo = simpleUser
				  		uni.setStorageSync('authKey',auth)
						uni.setStorageSync('userInfo',simpleUser)
						that.user = false
				  		// that.haveOrder()
				  	},
				  });
			    } else {
			      console.log('登录失败！' + res.errMsg)
			    }
			  }
			})
		},
		close(e){
			if(e==1){
				this.packets = false;
			}else{
				this.user = false;
				uni.showToast({
					title: '未微信授权',
					icon:'none',
					mask: false,
					duration: 1500
				});
			}
		},
		goActivity(){
			uni.navigateTo({
				url: '/pages/service/detail?id=28'
			})
		},
		closeActivity(){
			this.activity = false
		},
		closePromotion(){
			this.promotion = false
		},
		goService(){
			uni.switchTab({
				url: '/pages/service/service'
			})
			this.promotion = false
		},
		haveOrder: function() {
			var that = this;
			uni.request({
				url: this.http + 'order/list',
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {},
				success: res => {
					if (res.data.code == 200) {
						let list = res.data.data.list
						if (list.length == 0){
							that.promotion = true
						}
					}
				},
			});
		},
		// 请求知识分类
		requestLearn: function() {
			var that = this;
			uni.request({
				url: this.http+'knowledge/category',
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {},
				success: res => {
					if (res.data.code == 200) {
						that.tabBars = res.data.data
						that.symbolId = res.data.data[0].id;
						this.requestLearnList()
					}
				},
			});
		},
		// 检查会话是否失效
		checkSession: function() {
			var that = this;
			uni.request({
				url: this.http + 'user/checkSession',
				method: 'POST',
				header: {
					authKey: this.authKey
				},
				data: {},
				success: res => {
					if (res.data.code == 101) {
						that.authKey = ''
						uni.setStorageSync('authKey','')
					}
				},
			});
		},
		// 请求知识分类的列表
		requestLearnList: function(isLoading = false) {
			if (isLoading)	uni.showLoading({title: '加载中'})
			var that = this;
			let querys = "?start=" + this.start + "&size=" + this.size + "&keyword=" + this.keyword
			that.loadingType = 1
			uni.request({
				url: this.http+'knowledge/list' + querys,
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {
					category_id:that.symbolId
				},
				success: res => {
					let data = res.data.data
					if (res.data.code == 200) {
						let order = data.list
						if (order.length == 0) {
							order = []
						}
						
						if (that.start == 0) 
							that.lists = []
                          
						that.lists = that.lists.concat(order)
						if (that.lists.length == 0) 
							that.record = true
						else 
							that.record = false
						that.start = data.next
						if (order.length == 0 || order.length < this.size || that.start == -1) {
							that.loadingType = 2
						} else {
							that.loadingType = 0
						}
					}
					if (this.pullDown){
						uni.stopPullDownRefresh()
						this.pullDown = false
					}
				},
				fail: () => {
					console.log("请求失败")
				},
				complete: () => {
					if(isLoading) uni.hideLoading()
				}
			});
		},
		// 收藏
		collect(collect){
			var that = this;
			uni.request({
				url: this.http+'collection/append/' + collect.id,
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {
					
				},
				success: res => {
					console.log(res.data)
					if (res.data.code == 200) {
						collect.is_collect = 1
						uni.showToast({
							title: '收藏成功',
							icon:'none',
							mask: false,
							duration: 1500
						});
					} else if (res.data.code == 101){
						uni.setStorageSync('userInfo','')
						uni.setStorageSync('authKey','')
						that.user = true
					}
				},
			});
		},
		// 取消收藏
		cancel(collect){
			var that = this;
			uni.request({
				url: this.http+'collection/remove/' + collect.id,
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {
					
				},
				success: res => {
					console.log(res.data)
					if (res.data.code == 200) {
						uni.showToast({
							title: '取消收藏成功',
							icon:'none',
							mask: false,
							duration: 1500
						});
						collect.is_collect = 0
					}
				},
			});
		},
		goDetail(id){
			uni.navigateTo({
				url: '/pages/knowledge/detail?id=' + id
			});
		},
		lower(){
			if (this.loadingType == 2) {
				return
			}
			this.requestLearnList(false)
		}
	}
}
